<template>
    <div class="btn">
        <button :style="btnColor?(btnToColor?{'background': 'linear-gradient(to right,'+btnColor+','+ btnToColor+')'}:{'background': btnColor,}):''" @click="toDescription(productid)">立即购买</button>
    </div>
</template>
<script>
export default {
  props: ["productid","btnColor","btnToColor"],
  data() {
    return {};
  },
  methods: {
    toDescription(e) {
      this.$router.push({
        path: "description",
        query: {
          productid: this.productid,
					btnColor:this.btnColor,
					btnToColor:this.btnToColor,
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.btn {
  width: 100%;
  height: 3rem;
//   border: 1px red solid;
  margin-bottom: 3rem;
  button {
    
    position: fixed;
    bottom: 0;
    background-color: #ff8227;
    border-radius: .8rem;
    height: 3rem;
    color: white;
    margin: .6rem 0 1.1rem -40%;
    width: 80%;
    border: none;
    text-align: center;
    // text-decoration: none;
    // display: inline-block;
    // font-size: 16px;
		font-size:0.88rem ;
		left: 50%;
  }
}
</style>


