<template>
	<div class="errorlayout">
		<div class="errormsg">
        <p class="error-img"><img src="../assets/error.png" alt=""></p>
        <p class="error-tips">商品不存在</p>
    </div>
		<div class="rectoyou">
				<h2 class="ctitle"><span>为你推荐</span></h2>
				<div class="item-list">
            <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-immediate-check="true" infinite-scroll-distance="10">
                <li v-for="(item,index) in goods" :key="index">
                    <a class="item-box" :href="sixToken+'/?id='+item.id">
                        <div class="item-img"><img :src="item.image" alt="" /></div>
                        <h2 class="item-name">{{item.store_name}}</h2>
                        <p class="item-price"><span class="price-new"><i>¥</i><span>{{item.price}}</span></span></p>
                    </a>
                </li>
            </ul>
            <!--显示加载中状态-->
            <div class="loading-box" v-if="loading">
                <mt-spinner type="fading-circle" class="loading-more" color="#ff8227"></mt-spinner>
            </div>
            <div class="no-more" v-if="noMore">亲，已经到底了哦！</div>
				</div>
		</div>
	</div>
</template>

<script>
import Vue from 'vue';
import axios from "axios";
import { Spinner, InfiniteScroll } from 'mint-ui';
Vue.use(InfiniteScroll);
Vue.component(Spinner.name, Spinner);

export default {
  components: {
    'mt-spinner':Spinner,
  },
  data: function () {
    return {
      goods: [],
      loading: false, // 加载中转圈
      noMore: false, // 是否还有更多
      endTime: "",
      page: {
        current_page: 1, //当前页面
        per_page: 10, //每一页10个产品
        last_page: "", //总分页数
        total: "",//商品总数
        data: {
          id: "" //商品ID
        }
      },
      sixToken: this.GLOBAL.hSixURL
    }
  },
  created() {
    this.initData();
  },
  mounted() {
  },
  methods: {
    initData: function(type) {
      this.loading = true;
      axios
        .get("http://app.taijidjk69.com/api/h5/good/goodhot", {params: {page: this.page.current_page}})
        .then(response => {
          if (type === 'loadMore') {
            this.goods = this.goods.concat(response.data.data.data);
          } else {
            this.goods = response.data.data.data;
          }
          // 设置分页
          console.log(this.goods.concat(response.data.data.data));
          //商品总数
          this.page.total = response.data.data.total;
          console.log('商品总数', this.page.total);
          //总分页数
          this.page.last_page = Math.ceil(this.page.total / this.page.per_page);
          console.log('总分页数', Math.ceil(this.page.total / this.page.per_page));
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
        });

    },
    loadMore:function() {
      this.page.current_page += 1; // 增加分页
      this.loading = true;// 加载中
      if(this.page.current_page <= this.page.last_page ){
        //加载数据
        setTimeout(()=>{
          this.initData('loadMore');
        },200)
      }else{
        this.noMore = true; // 显示没有更多了
        this.loading = false; // 关闭加载中
        return false;
      }
      //console.log(this.page.current_page);
    },
    // 跳转项目详情页
    gotoGoodsDetail:function (value) {
      //this.$router.push({ path: "http://h6.taijidjk69.com/?id=" + value});
      //let routes = this.$router.resolve({ path: "http://h6.taijidjk69.com/?id=" + value });
      //window.open(routes.href, '_blank');
      //location='http://app.taijidjk69.com/api/h5/good/ProductAttrDetail?productId=' + value;
    }
  }

}
</script>

<style lang="scss" scoped>
.errormsg{
	width: 100%;
    margin-top: 50px;
    .error-tips{
      font-size: 18px;
      color: #a0a0a0;
      text-align: center;
      margin-top: 20px;
    }
   .error-img{
     width: 100%;
     text-align: center;
   }
  .error-img img{
    position: relative;
    width: 50%;
    height: 50%;
  }
}
.rectoyou {
  width: 100%;
  margin-top: 50px;
  .ctitle {
    padding: 0.5em 1em;
    color: #acacac;
    font-size: 14px;
  }
  .ctitle span {
    padding: 0.25em 0.25em 0.25em 0;
    border-bottom: #ff8227 solid 2px;
  }
  .item-list {
    width: 100%;
    /*height: 100%;*/
    /*overflow-y: auto;*/
    &::scroll-bar {
      width: 0;
    }
    ul {
      padding: 0 1em;
      margin: 0;
      list-style: none;
      display: -webkit-flex; /*Safari*/
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
    }
    li {
      width: 50%;
      height: 100%;
      margin-bottom: 1em;
      word-break: break-all;
      word-wrap: break-word;
      list-style: none;
      box-sizing: border-box;
    }
    li:nth-of-type(odd) .item-box { /*奇数行*/
      display:block;
      margin-right: 10px;
      text-decoration: none;
    }
    li:nth-of-type(even) .item-box { /*偶数行*/
      display:block;
      margin-left: 10px;
      text-decoration: none;
    }
    li .item-img {
      width: 100%;
      height: 10em;
      margin-bottom: 0.5em;
      overflow: hidden;
      img {
        position: relative;
        width: 100%;
        height: 100%;
      }
    }
    li .item-name {
      height: 24px;
      line-height: 24px;
      padding: 0;
      margin: 0;
      color: #666;
      font-size: 14px;
      font-weight: 400;
      text-overflow:ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
    li .item-price {
      line-height: 24px;
      padding: 0.25em 0 0;
      margin: 0;
      i {
        margin-right: 3px;
        font-weight: 400;
        font-size: 12px;
        font-style: normal;
        font-family: arial, sans-serif;
      }
      .price-new {
        margin-right: 10px;
        color: #fb4e44;
        font-size: 14px;
        font-weight: bold;
      }
      .price-origin {
        color: #b7bcb8;
        font-size: 12px;
        text-decoration: line-through;
      }
    }
  }
  .loading-box{
    width: 100%;
    line-height: 2em;
    margin-top: 2em;
    margin-bottom: 2em;
    text-align: center;
    .loading-more{
      display:inline-block;
      width:32px;
      height:32px;
      padding: 2em;
      margin: 0 auto;
      text-align: center;
    }
  }
  .no-more{
    width: 100%;
    line-height: 2em;
    margin-top: 2em;
    margin-bottom: 2em;
    color: #acacac;
    text-align: center;
  }
}
</style>



