<template>
    <div class="goods-detail">
				
        <!-- 商品不存在 -->
		<nodata-x v-if="error" :productid=productId></nodata-x>

        <!-- 商品详情内容 -->
        <div class="cont" v-html="goodInfo">
            <!-- <img class="good-img" src="../../../static/images/goodImg.jpg" alt=""> -->
        </div>

        <!-- 购买按钮 -->
        <buybtn-x v-if="errorBtn" :productid=productId :btnColor=btnColor :btnToColor=btnToColor></buybtn-x>
				
    </div>
</template>

<script>
import axios from "axios";
import nodata from "../../components/nodata";
import buybtn from "../../components/buybtn";

export default {
  components: {
    nodataX: nodata,
    buybtnX: buybtn
  },
  data() {
    return {
      goodInfo: "",
      productId: -1,
			error:false,//接口返回报错时为true,显示错误页面
			errorBtn:true,//接口返回报错时为false,隐藏按钮
			btnColor:'',
			btnToColor:'',
    };
  },
  created() {
    // let id= {id: 1}
    // let id={id:this.$route.query.productid};
    // id = JSON.stringify(id);
    // console.log(this.$route.query.id);
    // console.log(this.$route.meta.title);
    let id = this.getUrlParam("id");
    console.log(id);
    axios
      .get("http://app.taijidjk69.com/api/h5/good/details?id="+id)
      //.get("http://app.taijidjk69.com/api/h5/good/details?id="+"1111")
      .then(response => {
          console.log(response.data.data);
          this.goodInfo = response.data.data.description;
          this.productId = response.data.data.id;
					let colorStr=response.data.data.subcolor;
					let colorList= colorStr.split(",")//转换字符串为数组

					//判断是否为渐变按钮
					if(colorList.length>=2){
						this.btnColor=colorList[0];//若btnToColor不为空,此为渐变前方颜色,如btnToColor为空,则为按钮颜色,若此为空,则默认颜色
						this.btnToColor=colorList[1];//若btnToColor不为空,此为渐变后方颜色
					}else{
						this.btnColor=colorList[0];//若btnToColor不为空,此为渐变前方颜色,如btnToColor为空,则为按钮颜色,若此为空,则默认颜色
					}
					
		      document.title =response.data.data.store_name?response.data.data.store_name:this.$route.meta.title;
      })
      .catch(error => {
          console.log(error);
          this.error=true;
          this.errorBtn=false;
          document.title = '商品不存在 - '+this.$route.meta.title;
      });
  },
  methods: {
    /**
     *
     * 获取url参数对象
     * @param {any} str
     */
    getUrlParam(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        console.log(reg)
        var r = window.location.search.substr(1).match(reg);
        console.log(r)
        if (r != null) return unescape(r[2]);
        return null;
    }
  }
};
</script>

<style>
.cont {
  width: 100%;
}
.cont img {
  width: 100%;
}
.cont img:nth-child(3) {
  float: left;
}
p{
	margin: 0;
}
</style>

<style lang="scss" scoped>
.goods-detail {
  margin: 0;
  padding: 0;
}
</style>



